import React from "react";
import Boxes from "../helpers/Boxes";

const Header = ({ data }) => {
  const SummaryData = [
    { title: " ", value: data?.totalFlour },
    { title: " ", value: data?.restFlour },
    { title: " ", value: data?.totalAccount },
    { title: " ", value: data?.totalBreed },
    { title: " ", value: data?.totalPayed },
    { title: " ", value: data?.restAccount },
  ];
  return <Boxes SummaryData={SummaryData} />;
};

export default Header;
